body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  margin-top: 55px !important;
  background: #ececec !important; }
  body .text-warning {
    color: #f79400 !important; }
  body .bg-warning {
    background-color: #f79400 !important; }
  body .navbar-dark .navbar-nav .nav-link {
    color: rgba(255, 255, 255, 0.75) !important; }
    body .navbar-dark .navbar-nav .nav-link:hover, body .navbar-dark .navbar-nav .nav-link:focus, body .navbar-dark .navbar-nav .nav-link.active {
      color: rgba(255, 255, 255, 0.95) !important; }

.beer-item .details {
  min-height: 330px;
  transition: all 0.2s ease-in-out !important;
  box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.25); }
  .beer-item .details:hover {
    cursor: pointer;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.5); }
  .beer-item .details .btn-fav.btn {
    color: #cccccc;
    transition: all 0.3s;
    position: absolute;
    right: 0;
    top: -5px;
    z-index: 999; }
    .beer-item .details .btn-fav.btn:hover, .beer-item .details .btn-fav.btn.active {
      color: #f79400; }

.beer-item .beer-thumbnail {
  max-height: 150px;
  width: auto; }

.beer-detail {
  color: #737373 !important;
  font-size: 1.1rem; }
  .beer-detail img {
    max-height: 450px;
    width: auto; }
  .beer-detail .beer-tagline {
    color: #c0c0c0; }
  .beer-detail .divider {
    display: inline-block;
    height: 5px;
    width: 100px;
    background-color: #b33e9a; }

.similar-beer {
  border: 1px solid #cccccc;
  min-height: 245px; }
  .similar-beer img {
    max-height: 140px;
    width: auto; }

.beer-icon {
  font-size: 400px;
  color: #e5e5e5; }

